window.switchLight = function (flashlightButton) {
	flashlightButton.classList.toggle("view_off");
	const heroBackground = document.getElementById("heroBackground");
	heroBackground.classList.toggle("view_lightOff")
	const hero = document.getElementById("hero");
	hero.classList.toggle("view_lightOff")
}

